import Drawer from '@material-ui/core/Drawer';
import {makeStyles} from '@material-ui/core/styles';
import React from 'react';
import filter_icon from '../images/icons/filter_icon.svg'
import FilterCard from '../modules/Gallery/components/FilterCard';

import "./SidebarDrawer.css"

const useStyles = makeStyles({
  list: {
    width: 250,
  },
  fullList: {
    width: 'auto',
  },
});

export default function SidebarDrawer() {
  const classes = useStyles();
  const [state, setState] = React.useState(false);

  const pathname = window.location.hash

  const toggleDrawer = (open: boolean) => (
    event: React.KeyboardEvent | React.MouseEvent,
  ) => {
    if (
      event.type === 'keydown' &&
      ((event as React.KeyboardEvent).key === 'Tab' ||
        (event as React.KeyboardEvent).key === 'Shift')
    ) {
      return;
    }

    setState(!state);
  };

  return (
    <div>
      <React.Fragment>
        <div onClick={toggleDrawer(true)} id={'filter-sidebar'}>
          <div className="sort-by" style={{color: '#7E7E8F'}}>Filter</div>
          <img src={filter_icon} alt={'filter_icon'}/>
        </div>
        <Drawer anchor={'left'} open={state} onClose={toggleDrawer(false)}>
          <FilterCard/>
        </Drawer>
      </React.Fragment>
    </div>
  );
}
