import {Accordion, AccordionDetails, AccordionSummary, Checkbox, Divider, FormControlLabel} from "@material-ui/core"
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import {useState} from "react";

import BodyIcon from "../../../images/icons/BodyIcon.svg"
import Offhand_front_Spear from "../../../images/icons/Offhand_front_Spear.png"
import RemoveFilterClose from "../../../images/icons/RemoveFilterClose.svg"
import StarCheckbox from "../../../images/icons/StarCheckbox.svg"
import StarCheckedbox from "../../../images/icons/StarCheckedbox.svg"
import "./FilterCard.css"

const FilterOptions = [
  {
    title: 'FRONT',
    options: ['Front Spear', 'Front Flame', 'Front Card', 'Front Flame', 'Front Card', 'Front Spear', 'Front Card', 'Front Spear', 'Front Card']
  },
  {
    title: 'FACE',
    options: ['Front Spear', 'Front Flame', 'Front Card', 'Front Flame', 'Front Card', 'Front Spear', 'Front Card', 'Front Spear', 'Front Card']
  }
]

const FilterCard = () => {
  const [expanded, setExpanded] = useState<string | false>(false);

  const handleFilterAccordion = (panel: string) => (event: React.ChangeEvent<{}>, expanded: boolean) => {
    setExpanded(expanded ? panel : false);
  };

  return (
    <div id="FilterCard">
      {
        FilterOptions.map((filterItem: any, i: number) =>
          <Accordion key={i} style={{margin: '0px'}} expanded={expanded === i.toString()}
                     onChange={handleFilterAccordion(i.toString())}>
            <AccordionSummary
              expandIcon={<ExpandMoreIcon/>}
              aria-controls="panel1a-content"
              id="panel1a-header"
            >
              <img src={BodyIcon} alt={'BodyIcon'} className={'mr10'}/>
              <div className='accordion-title'>{filterItem.title}</div>
            </AccordionSummary>
            <Divider/>
            <AccordionDetails style={{flexDirection: 'column', paddingTop: '12px'}}>
              {
                filterItem.options.map((option: any, i: number) =>
                  <div key={i} className="mb8 flex-space-between">
                    <div className="d-flex">
                      <img className="mr8" src={Offhand_front_Spear} alt={'Offhand_front_Spear'}/>
                      <div className="accordion-option">Front Spear</div>
                      <div className="accordion-option-count">(300)</div>
                    </div>
                    <FormControlLabel
                      style={{margin: '0px',}}
                      control={
                        <Checkbox icon={<img className="filter-checkbox" src={StarCheckbox} alt={'StarCheckbox'}/>}
                                  checkedIcon={<img className="filter-checkbox" src={StarCheckedbox}
                                                    alt={'StarCheckbox'}/>} name="checkedH"/>}
                      label=""
                    />
                  </div>
                )
              }

            </AccordionDetails>
          </Accordion>
        )
      }
      <Accordion style={{margin: '0px', paddingTop: '100px'}} expanded={true}>
        <AccordionDetails className="filter-card-accordion">
          <div className="filter-bottom-card">
            <div>
              <div style={{color: '#9A9AAF'}} className={'sort-by'}>Filters</div>
              <div className="accordion-option">Clear all</div>
            </div>
            <div style={{borderBottom: '1px solid #F2F3F7'}}></div>
            <div>
              <div style={{display: 'flex', marginBottom: '12px'}}>
                <div style={{color: '#656575'}} className={'accordion-title'}>Front:</div>
                <div style={{marginLeft: '10px', display: 'flex'}}>
                  <div style={{color: '#656575'}} className={'accordion-option-count'}>Spear</div>
                  <img src={RemoveFilterClose} alt={'RemoveFilterClose'}/>
                </div>
                <div style={{marginLeft: '10px', display: 'flex'}}>
                  <div style={{color: '#656575'}} className={'accordion-option-count'}>Flame</div>
                  <img src={RemoveFilterClose} alt={'RemoveFilterClose'}/>
                </div>
              </div>
              <div style={{display: 'flex'}}>
                <div style={{color: '#656575'}} className={'accordion-title'}>Front:</div>
                <div style={{marginLeft: '10px', display: 'flex'}}>
                  <div style={{color: '#656575'}} className={'accordion-option-count'}>Spear</div>
                  <img src={RemoveFilterClose} alt={'RemoveFilterClose'}/>
                </div>
                <div style={{marginLeft: '10px', display: 'flex'}}>
                  <div style={{color: '#656575'}} className={'accordion-option-count'}>Flame</div>
                  <img src={RemoveFilterClose} alt={'RemoveFilterClose'}/>
                </div>
              </div>
            </div>

          </div>
        </AccordionDetails>
      </Accordion>

    </div>
  )
}

export default FilterCard
